<template>
  <div class="app-container">
    <div class="filter-container">
      <el-select
        v-model="listQuery.accessPointTypeId"
        placeholder="类型"
        clearable
        class="filter-item"
        style="width: 130px"
        size="small"
      >
        <el-option v-for="item in codeOptions" :key="item.id" :label="item.name" :value="item.id" />
      </el-select>
      <el-input
        v-model="listQuery.name"
        placeholder="名称"
        style="width: 200px;"
        class="filter-item"
        size="small"
        @keyup.enter.native="handleFilter"
      />
      <el-select
        v-model="listQuery.status"
        class="filter-item"
        placeholder="状态"
        size="small"
        clearable
      >
        <el-option
          v-for="item in statusTypeOptions"
          :key="item.value"
          :label="item.lable"
          :value="item.value"
        />
      </el-select>
      <el-button
        v-waves
        class="filter-item"
        type="primary"
        icon="el-icon-search"
        size="small"
        @click="handleFilter"
      >搜索</el-button>
      <router-link to="create" v-if="checkPermission(['ADMIN'])">
        <el-button class="filter-item" type="primary" icon="el-icon-edit-outline" size="small">添加</el-button>
      </router-link>
    </div>
    <el-table
      v-loading="listLoading"
      :data="list"
      element-loading-text="Loading"
      stripe
      fit
      highlight-current-row
    >
      <el-table-column align="center" label="序号" width="75">
        <template slot-scope="scope">{{ scope.$index + 1 }}</template>
      </el-table-column>
      <el-table-column label="名称" align="center">
        <template slot-scope="scope">{{ scope.row.name }}</template>
      </el-table-column>
      <el-table-column label="地址">
        <template slot-scope="scope">/api/{{ scope.row.code }}/v1/{{ scope.row.id }}</template>
      </el-table-column>
      <el-table-column label="单价(元)" width="110" align="center">
        <template slot-scope="scope">
          <el-tag :type="scope.row.price | priceFilter" size="small">
            {{
            parseFloat(scope.row.price) / 100.0
            }}
          </el-tag>
        </template>
      </el-table-column>
      <el-table-column class-name="status-col" label="状态" align="center">
        <template slot-scope="scope">
          <span v-if="checkPermission(['ADMIN'])">
            {{
            statusMap[scope.row.status]
            }}
          </span>
          <span v-if="checkPermission(['TENANT'])">
            {{
            scope.row.tenantId == null ? "未开通" : "已开通"
            }}
          </span>
        </template>
      </el-table-column>
      <el-table-column
        v-if="checkPermission(['ADMIN'])"
        label="操作"
        align="center"
        class-name="small-padding fixed-width"
      >
        <template slot-scope="{ row }">
          <router-link :to="'edit/' + row.id" v-if="checkPermission(['ADMIN'])">
            <el-button size="mini" type="primary" icon="el-icon-edit">编辑</el-button>
          </router-link>
          <el-button
            icon="el-icon-delete"
            type="danger"
            v-if="checkPermission(['ADMIN'])"
            size="mini"
            style="margin-left: 10px;"
            @click="handleModifyStatus(row)"
          >{{ 0 === row.status ? "启用" : "停用" }}</el-button>
          <!--          <el-button-->
          <!--            v-if="row.tenantId == null && checkPermission(['TENANT'])"-->
          <!--            size="mini"-->
          <!--            @click="handleGrant(row)"-->
          <!--          >-->
          <!--            开通-->
          <!--          </el-button>-->
        </template>
      </el-table-column>
    </el-table>
    <pagination
      :total="total"
      :page.sync="listQuery.pageNo"
      :limit.sync="listQuery.pageSize"
      @pagination="fetchData"
    />
  </div>
</template>

<script>
import { list, update, grant, accessType } from "@/api/accessPoint";
import checkPermission from "@/utils/permission"; // 权限判断函数
import Pagination from "@/components/Pagination";
import waves from "@/directive/waves"; // waves directive
const statusTypeOptions = [
  { value: 1, lable: "正常" },
  { value: 0, lable: "停用" }
];
export default {
  components: { Pagination },
  directives: { waves },
  filters: {
    priceFilter(price) {
      let val = "success";
      if (price === 0) {
        val = "warning";
      } else if (price < 0) {
        val = "danger";
      }
      return val;
    }
  },
  data() {
    return {
      statusTypeOptions,
      total: 0,
      codeOptions: [],
      listQuery: {
        name: "",
        status: 1,
        accessPointTypeId: undefined,
        pageNo: 1,
        pageSize: 20
      },
      list: null,
      listLoading: true,
      statusMap: {
        1: "正常",
        0: "停用"
      }
    };
  },
  created() {
    accessType({ status: 1 }).then(response => {
      if (response.code === 0) {
        this.codeOptions = response.data.record;
        this.fetchData();
      } else {
        this.$message({
          message: response.message,
          type: "warning"
        });
      }
    });
  },
  methods: {
    checkPermission,
    fetchData() {
      this.listLoading = true;
      list(this.listQuery).then(response => {
        if (response.code === 0) {
          this.list = response.data.record;
          this.total = response.data.total;
        } else {
          this.$message({
            message: response.message,
            type: "warning"
          });
        }
        this.listLoading = false;
      });
    },
    handleFilter() {
      this.fetchData();
    },
    handleGrant(row) {
      grant(row.id).then(response => {
        if (response.code === 0) {
          row.tenantId = 1;
          this.$message({
            message: "开通成功",
            type: "success"
          });
        } else {
          this.$message({
            message: response.message,
            type: "warning"
          });
        }
      });
    },
    handleModifyStatus(row) {
      row.status = row.status === 0 ? 1 : 0;
      update(row).then(response => {
        if (response.code === 0) {
          this.$message({
            message: "操作成功",
            type: "success"
          });
        } else {
          this.$message({
            message: response.message,
            type: "warning"
          });
          row.status = row.status === 0 ? 1 : 0;
        }
      });
    }
  }
};
</script>
